.section3_2.container {
  width: 320px;
  padding: 0px;
  ul {
    li.col-xs-6 {
      border-radius: 6px 0 0 0;
      width: 48%;
      display: block;
      text-align: center;
      font-size: 16px;
      padding: 0px;
      background-color: #f6f6f6;
      &:first-child {
        border-radius: 0 6px 0 0;
        margin-right: 4%; }
      a {
        padding: 10px 20px;
        vertical-align: middle;
        border: none;
        font-size: 16px;
        height: 65px;
        color: #7b7b7b; } } }
  .nav.nav-tabs {
    width: 100%;
    border: none;
    margin: auto; }
  ol {
    padding: 0px;
    list-style-position: inside;
    li {
      font-size: 24px;
      font-weight: bold;
      img, p {
        width: 100%;
        font-size: 16px;
        font-weight: 400; } } }
  .nav.nav-tabs>li.active>a {
    color: black;
    font-weight: bold;
    background-color: #eee;
    &:focus, &:hover {
      background-color: #eee; }

    ol {
      list-style: none;
      counter-reset: item; }
    ol {
      li:before {
        content: counters(item, ".") ". ";
        counter-increment: item 3; } } } }
@media screen and (min-width: 768px) {
  .section3_2.container {
    width: 768px; } }
@media screen and (min-width: 768px) {
  .up {
    .tab-content {
      padding: 0px;
      .swap_mode {
        padding-top: 52px; } }
    ol {
      padding: 70px 44px;
      .switch {
        display: flex;
        justify-content: center;
        .images {
          padding-top: 38px;
          height: 50%;
          width: 50%;
          order: 2; }
        .text_p {
          padding-top: 38px;
          order: 1;
          width: 50%; } }
      .swap_mode {
        padding-top: 52px; } } }

  // .nav.nav-tabs
 }  //   width: 768px
@media screen and (min-width: 1140px) {
  .section3_2.container {
    width: 1140px;
    margin: auto; }

  .tab-content {
    width: 100%;
    background-color: #eee;
    // width: 1140px
 } }    // margin: auto

.tab-content {
  padding: 0px 0px 0px 0px;
  width: 100%;
  background-color: #eee;
  .swap_mode {
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    a {
      padding-bottom: 20px;
      img {
        width: 30px; }
      span {
        padding-left: 13px; } } }
  @media screen and (min-width: 1140px) {
    .section3_2.container {
      width: 1140px;
      margin: auto; } } }
