.section3.container {
  padding: 15px 0px 45px 0px;
  width: 320px;
  .up {
    padding: 0px;
    width: 100%;
    ol {
      padding: 0px 17px 0px 17px;
      list-style-position: inside;
      padding-bottom: 23px;
      li {
        font-size: 24px;
        font-weight: bold;
        p {
          padding: 23px 0px 40px 0px;
          margin-left: 0px;
          font-size: 16px;
          font-weight: 400; }
        img {
          padding-top: 32px;
          width: 100%; } } }

    ol li img, p {
      width: 100%;
      font-size: 16px;
      font-weight: 400; }

    ol:first-child {
      list-style: none;
      counter-reset: item;
      li {
        display: block;
        list-style-type: none; }
      li:before {
        content: counters(item, ".") ". ";
        counter-increment: item; } }
    ol:last-child {
      li {
        list-style: disc; } } }
  @media screen and (min-width: 768px) {
    width: 768px;
    .tab-pane.active {
      width: 768px;
      margin: auto; }
    .up {
      ol {
        padding: 0px 44px 0px 44px;
        .switch {
          display: flex;
          justify-content: center;
          .images {
            height: 50%;
            width: 50%;
            order: 2; }
          .text_p {
            padding-right: 20px;
            order: 1;
            width: 50%; } } } } }
  @media screen and (min-width: 1140px) {
    width: 100%;
    .tab-content {
      background-color: #eee;
      width: 100%; }
    .tab-pane.active {
      width: 1140px;
      margin: auto; }
    ol .switch .text_p {
      padding-right: 33px; }
    .up {
      width: 100%;
      ol {
        width: 1140px;
        margin: auto; } } } }
